"use client";

import React, { useState } from "react";
import Stepper from "@/components/Stepper";
import { Step } from "@/components/Stepper/types";
import PersonalInformationStep from "./components/steps/PersonalInformationStep";

const steps: Step[] = [
  { id: 1, label: "Datos personales", completed: false },
  { id: 2, label: "Datos de empresa", completed: false },
  { id: 3, label: "Verificación", completed: false },
  { id: 4, label: "Confirmación", completed: false },
];

const OnboardingLayout = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [stepsState, setStepsState] = useState<Step[]>(steps);

  const handleNext = () => {
    if (currentStep < steps.length - 1) {
      const newSteps = [...stepsState];
      newSteps[currentStep].completed = true;
      setStepsState(newSteps);
      setCurrentStep(currentStep + 1);
    }
  };

  const handleBack = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1);
    }
  };

  const handleStepChange = (step: number) => {
    setCurrentStep(step);
  };

  const renderStep = () => {
    switch (currentStep) {
      case 0:
        return (
          <PersonalInformationStep
            onNext={handleNext}
            onBack={handleBack}
            isFirstStep={currentStep === 0}
            isLastStep={currentStep === steps.length - 1}
          />
        );
      // Aquí irán los demás casos para los otros pasos
      default:
        return null;
    }
  };

  return (
    <section className="bg-[#1249C7] min-h-screen w-screen py-8">
      <div className="container mx-auto flex flex-col items-center">
        <div className="w-full max-w-6xl mb-8">
          <Stepper
            steps={stepsState}
            currentStep={currentStep}
            onStepChange={handleStepChange}
          />
        </div>
        {renderStep()}
      </div>
    </section>
  );
};

export default OnboardingLayout;
